html {
  box-sizing: border-box; }

body {
  box-sizing: border-box;
  padding: 0;
  margin: 0; }
  body * {
    box-sizing: border-box;
    font-family: 'Titillium Web', sans-serif; }

.header {
  height: 50px;
  width: 600px;
  position: fixed;
  max-width: 100%;
  left: 50%;
  transform: translateX(-50%);
  top: 0;
  padding: 0 15px;
  background: #f9f6f6;
  box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.5);
  padding-top: 8px;
  z-index: 10; }
  .header .button {
    padding: 7px 13px; }

.wraper {
  width: 600px;
  max-width: 100%;
  margin: 0 auto; }

.app {
  width: 600px;
  margin: 0 auto;
  margin-top: 50px;
  max-width: 100%;
  height: calc(50vh - 50px);
  padding: 20px 15px;
  background: #7db9e8;
  background: -moz-radial-gradient(center, ellipse cover, #7db9e8 0%, #0d4e99 100%);
  background: -webkit-radial-gradient(center, ellipse cover, #7db9e8 0%, #0d4e99 100%);
  background: radial-gradient(ellipse at center, #7db9e8 0%, #0d4e99 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#7db9e8', endColorstr='#0d4e99',GradientType=1 ); }

.loading {
  position: fixed;
  width: 100%;
  height: 100vh;
  background: #0d202f;
  top: 0;
  left: 0;
  display: none;
  z-index: 25; }
  .loading:before {
    content: attr(data-message);
    position: absolute;
    top: 85%;
    width: 100%;
    color: #ffffff;
    text-align: center;
    transform: translateY(-50%); }
  .loading.show {
    display: block; }
  .loading .percent {
    position: absolute;
    width: 80%;
    height: 5px;
    left: 50%;
    top: 90%;
    transform: translateX(-50%) translateY(-50%);
    border: 1px solid #ffffff;
    border-radius: 3px;
    overflow: hidden; }
    .loading .percent span {
      display: block;
      width: 0;
      height: 100%;
      background: #ffffff;
      transition: width 0.1s ease; }

.control-form {
  position: fixed;
  top: 50px;
  width: 100%;
  height: calc(100vh - 50px);
  left: -101vw;
  transition: left 0.5s ease;
  background: #ffffff;
  padding: 15px;
  box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.5); }
  .control-form > * {
    margin: 20px 0; }
  .control-form.show {
    left: 0; }
  .control-form input {
    padding: 10px;
    background: transparent;
    border: 0;
    border-bottom: 1px solid #000000; }
  .control-form #data-get-coords {
    width: 100%; }
  .control-form #city {
    width: 40%; }
  .control-form #data-get-coords-city {
    width: 55%;
    float: right; }

.button {
  padding: 10px;
  background: #f9f6f6;
  border: 0;
  outline: none;
  box-shadow: 0 0 2px 0px rgba(0, 0, 0, 0.5); }
  .button:active {
    box-shadow: inset 0 0 2px 0 rgba(0, 0, 0, 0.5); }

.weater-widget {
  position: relative;
  width: 100%;
  height: 100%; }
  .weater-widget .icon {
    width: 180px;
    height: 180px;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%); }
    .weater-widget .icon svg {
      width: 100%;
      height: 100%; }
  .weater-widget .temp {
    color: #ffffff;
    font-size: 24px;
    text-align: right;
    font-weight: bold;
    position: absolute;
    right: 0;
    top: 0;
    padding: 10px;
    background: rgba(0, 0, 0, 0.2);
    border-radius: 15px; }
    .weater-widget .temp span {
      font-weight: 300;
      font-size: 16px; }
  .weater-widget .temp-var {
    position: absolute;
    bottom: 0;
    color: #ffffff;
    width: 100%;
    font-size: 16px; }
    .weater-widget .temp-var span {
      font-size: 12px; }
    .weater-widget .temp-var .ic {
      position: absolute;
      right: 0;
      bottom: 0;
      float: right;
      width: 75px;
      height: 75px; }
      .weater-widget .temp-var .ic svg {
        width: 100%;
        height: 100%; }
  .weater-widget .city {
    color: #ffffff;
    font-size: 18px;
    font-weight: bold; }
  .weater-widget .today {
    font-size: 12px;
    color: #ffffff;
    font-weight: 300; }

.days-widget {
  margin: 0 -15px;
  margin-top: 20px;
  padding-top: 5px;
  max-height: 50vh;
  overflow-y: scroll; }
  .days-widget:after {
    content: '';
    display: block;
    clear: both; }
  .days-widget .w-item {
    display: inline-block;
    width: calc(50% - 10px);
    margin: 5px;
    float: left;
    padding: 15px 0;
    min-height: 152px;
    border: 1px solid rgba(103, 164, 216, 0.43);
    border-radius: 5px;
    background: linear-gradient(to bottom, #ffffff 0, #70abef 100%); }
    .days-widget .w-item .date {
      font-size: 14px;
      padding: 0 15px; }
    .days-widget .w-item .icon, .days-widget .w-item .temp {
      width: 48%;
      font-size: 14px;
      display: inline-block;
      vertical-align: middle; }
    .days-widget .w-item .icon svg {
      width: 100%;
      height: 100%; }

.informer {
  position: fixed;
  z-index: 100;
  background: #1a2e94;
  color: #ffffff;
  width: 600px;
  bottom: -100vh;
  transition: bottom 0.5s ease;
  max-width: 100%;
  min-height: 50px;
  padding: 15px; }
  .informer i {
    font-size: 0;
    color: transparent;
    position: absolute;
    top: 15px;
    right: 15px;
    width: 20px;
    height: 20px; }
    .informer i:before, .informer i:after {
      content: '';
      position: absolute;
      width: 100%;
      height: 0;
      border: 1px solid #ffffff;
      top: 50%; }
    .informer i:before {
      transform: rotate(45deg); }
    .informer i:after {
      transform: rotate(-45deg); }
  .informer.show {
    bottom: 0; }
